import React from 'react'
import { DropTarget } from 'react-dnd'

// Drag sources and drop targets only interact
// if they have the same string type.
// You want to keep types in a separate file with
// the rest of your app's constants.
const Types = {
    CARD: 'card'
}

const calcIndex = function(pos){
    let candidates = document.getElementsByClassName("factsheetSlot");
        
    const boderGap = 60;
    let index = 0;
    let prev;
    let insert;
    for(let slot of candidates){
        let box = slot.getBoundingClientRect();
        if(index === 0 || Math.floor(box.y) !== Math.floor(prev.y)){//first one in one row 
            if(pos.x <= box.left + box.width/2 && pos.x >= box.left - boderGap && pos.y >= box.top && pos.y <= box.bottom){
                return { insert: index }
            }
            if(index === 0 && pos.y >= box.top - boderGap && pos.y < box.top){//title
                return { insert: index }
            }
            if(prev && pos.y > prev.bottom && pos.y < box.top){//between row
                return { insert: index }
            }
            if(prev && pos.x <= prev.right + boderGap && pos.x >= prev.right - prev.width/2 && pos.y >= prev.top && pos.y <= prev.bottom){//last one in one row
                return { insert: index }
            }
        }else{
            if(pos.x <= box.left + box.width/2 && pos.x >= prev.right - prev.width/2 && pos.y >= box.top && pos.y <= box.bottom){
                return { insert: index }
            }
        }
        index ++;
        prev = box;
    }
    if(!prev) return {insert:0}
    //last one
    if(!insert && prev && pos.x <= prev.right + boderGap && pos.x >= prev.right - prev.width/2 && pos.y >= prev.top && pos.y <= prev.bottom){
        insert = index;
    }
    if(pos.y > prev.bottom && pos.y <= prev.bottom + boderGap){
        insert = index;
    }

    return { insert: insert }
}
/**
 * Specifies the drop target contract.
 * All methods are optional.
 */
const cardDropTarget = {
    hover(props, monitor, component) {
        // This is fired very often and lets you perform side effects
        // in response to the hover. You can't handle enter and leave
        // hereif you need them, put monitor.isOver() into collect() so you
        // can use componentDidUpdate() to handle enter/leave.

        // You can access the coordinates if you need them
        // const clientOffset = monitor.getClientOffset()
        // const componentRect = findDOMNode(component).getBoundingClientRect()

        // // You can check whether we're over a nested drop target
        // const isOnlyThisOne = monitor.isOver({ shallow: true })

        // // You will receive hover() even for items for which canDrop() is false
        // const canDrop = monitor.canDrop()
        let pos = monitor.getClientOffset();
        component.props.setHoverIndex(calcIndex(pos).insert);
    },

    drop(props, monitor, component) {
        if (monitor.didDrop()) {
            // If you want, you can check whether some nested
            // target already handled drop
            return
        }

        // Obtain the dragged item
        component.props.setHoverIndex(-1);
        let pos = monitor.getClientOffset();
        return calcIndex(pos);
    }
}

/**
 * Specifies which props to inject into your component.
 */
function collect(connect, monitor) {
  return {
    // Call this function inside render()
    // to let React DnD handle the drag events:
    connectDropTarget: connect.dropTarget(),
    // You can ask the monitor about the current drag state:
    isOver: monitor.isOver(),
    // isOverCurrent: monitor.isOver({ shallow: true }),
    canDrop: monitor.canDrop(),
    // itemType: monitor.getItemType()
  }
}

class cardDrop extends React.Component{
    constructor(props) {
        super(props);
        this.containerRef = React.createRef()
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.isOver && this.props.isOver) {
        // You can use this as enter handler
        }

        if (prevProps.isOver && !this.props.isOver) {
        // You can use this as leave handler
        }

        if (prevProps.isOverCurrent && !this.props.isOverCurrent) {
        // You can be more specific and track enter/leave
        // shallowly, not including nested targets
        }
    }

    render() {
        // Your component receives its own props as usual
        const { children } = this.props

        // These props are injected by React DnD,
        // as defined by your `collect` function above:
        const { connectDropTarget } = this.props
        return connectDropTarget && connectDropTarget(
            children
        )
    }
}

export default DropTarget(
  Types.CARD,
  cardDropTarget,
  collect
)(cardDrop)
