import React from 'react';
import { Layout, Divider,Spin } from "antd";
import { CaretLeftOutlined, CaretRightOutlined } from "@ant-design/icons"

import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import DropView from './DropView';
import HeadBarView from "@/components/HeadBar";
import './index.less';

import SplitPane from 'react-split-pane';
import Pane from 'react-split-pane/lib/Pane';
import OperationType from '@/constant/OperationType'
const { Header, Sider } = Layout;

export default class EditView extends React.Component {
    constructor(props) {
        super(props);     
        this.state = {
            leftPaneWidth:'310px',
            rightPaneWidth:'21%'

        }
      }

    changeLeftPane=()=>{
        let leftWidth=this.props.editstate?document.getElementsByClassName('leftPannel1')[0].getBoundingClientRect().width+'px':document.getElementsByClassName('leftPannel')[0].getBoundingClientRect().width+'px'
        let rightWidth=document.getElementsByClassName('leftSymmetryPannel')[0].getBoundingClientRect().width+'px'
       this.props.updatePannelWidth(leftWidth,rightWidth)
    }
    render() {
        const {initDone,intl, editstate,updateEdit,  } = this.props;
        let leftLayout, rightLayout, leftSymmetryLayout;
        let leftLayout1;
        // 
        let now = new Date().getTime();
        // 
        let leaveTime = parseInt(localStorage.getItem('leaveTime'), 10);
        // 5s
        let refresh = (now - leaveTime) <= 3000;
        // alert
        if(refresh){
            this.props.updateOperation(OperationType.BEFORE_UPLOAD)
            this.props.history.push("/")
        }
        window.onbeforeunload = function(e){
            if(e) e.returnValue=("");
            return "??"

        }
        window.onunload = function(){
            localStorage.setItem('leaveTime', new Date().getTime());
            };
        this.props.children.forEach(child => {
            if (child.props.layoutposition === 'left') {
                leftLayout = child;
            } else if (child.props.layoutposition === 'right') {
                rightLayout = child;
            } else if (child.props.layoutposition === 'leftSymmetry') {
                leftSymmetryLayout = child;
            } else if (child.props.layoutposition === 'left1') {
                leftLayout1 = child;
            }
        });
        let tip = initDone && intl.get("Generating...");
        return <div className='EditPage'>
            <Header style={{ height: "50px" }}>
                <HeadBarView isLogIn={false} {...this.props} />
            </Header>
            <Spin tip={tip} spinning={updateEdit} wrapperClassName="regenerateSpining">
            
            <Divider className="customDivider" />
            <DndProvider backend={HTML5Backend} options={{ enableMouseEvents: true }} className='splitPane-box'>

                <Layout style={{ height: "calc(100% - 50px)" }}>
                    <SplitPane split="vertical" onChange={this.changeLeftPane}>
                        <Pane initialSize={this.props.leftPaneWidth} maxSize="310px" >
                            {
                                editstate ? (<div className='leftPannel1'>{leftLayout1}</div>) : (<div className="leftPannel">{leftLayout}</div>)
                            }
                            {/* <LeftPanelFinal/> */}
                        </Pane>
                        <div className='rightPannel'>
                            <DropView {...this.props}>
                                {rightLayout}
                            </DropView>
                        </div>

                        <Pane initialSize={this.props.rightPaneWidth} >
                            <div className='leftSymmetryPannel'>
                                {leftSymmetryLayout}
                            </div>
                        </Pane>
                    </SplitPane>
                </Layout>
           
            </DndProvider>
                 </Spin>
        </div >
    }
}